// src/components/exercise-common/ExerciseFooter/index.js - AVEC PROPTYPES

import { View, Text, TouchableOpacity } from "react-native";
import PropTypes from 'prop-types';
import { Ionicons } from "@expo/vector-icons";
import createStyles from "./style";

/**
 * Pied de page standardisé pour tous les écrans d'exercices 
 * avec boutons de navigation et validation
 */
const ExerciseFooter = ({
  primaryLabel,
  secondaryLabel,
  onPrimaryPress,
  onSecondaryPress,
  showSecondary,
  showCheck,
  isDisabled,
  color = "#3b82f6",
  isLastQuestion,
  hideSkip,
  onSkip,
  skipLabel,
}) => {
  // Ajustement du label pour le dernier exercice
  const finalPrimaryLabel = isLastQuestion ? 'Terminer' : primaryLabel;

  return (
    <View style={styles.container}>
      <View style={styles.buttonRow}>
        {showSecondary ? (
          <TouchableOpacity
            style={styles.secondaryButton}
            onPress={onSecondaryPress}
          >
            <Ionicons name="chevron-back" size={20} color="#6B7280" />
            <Text style={styles.secondaryButtonText}>{secondaryLabel}</Text>
          </TouchableOpacity>
        ) : (
          <View style={{ flex: 1 }} />
        )}

        {!hideSkip && onSkip && (
          <TouchableOpacity 
            style={styles.skipButton} 
            onPress={onSkip}
          >
            <Text style={styles.skipText}>{skipLabel}</Text>
          </TouchableOpacity>
        )}

        <TouchableOpacity
          style={[
            styles.primaryButton,
            { backgroundColor: color },
            isDisabled && styles.disabledButton
          ]}
          onPress={onPrimaryPress}
          disabled={isDisabled}
        >
          <Text style={styles.primaryButtonText}>{finalPrimaryLabel}</Text>
          {showCheck ? (
            <Ionicons name="checkmark" size={20} color="white" />
          ) : (
            <Ionicons name="chevron-forward" size={20} color="white" />
          )}
        </TouchableOpacity>
      </View>
    </View>
  );
};

// PropTypes pour le composant ExerciseFooter
ExerciseFooter.propTypes = {
  primaryLabel: PropTypes.string.isRequired,
  secondaryLabel: PropTypes.string,
  onPrimaryPress: PropTypes.func.isRequired,
  onSecondaryPress: PropTypes.func,
  showSecondary: PropTypes.bool,
  showCheck: PropTypes.bool,
  isDisabled: PropTypes.bool,
  color: PropTypes.string,
  isLastQuestion: PropTypes.bool,
  hideSkip: PropTypes.bool,
  onSkip: PropTypes.func,
  skipLabel: PropTypes.string,
};

export default ExerciseFooter;
